
<template>
  <el-main style="text-align:left; line-height: 1.8em;">
    <h3 style="text-align:center">
      
    </h3>
      <el-form ref="form" :model="form" :rules="rules"  label-width="80px" class="demo-ruleForm" style="width:300px; margin:auto">
        <el-form-item label="" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm()"></el-button>
          <el-button @click="resetForm()"></el-button>
        </el-form-item>
      </el-form>
      <div id='infoLogin' style="text-align:center; color:#f00">
      </div>
    </el-main>
</template>
<script>
import CryptoJS from 'crypto-js'
export default {
  data () {
    return {
      infoLogin: false,
      form: {
        username: '',
        password: ''
      },
      userToken: '',
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 20, message: ' 8  20 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 20, message: ' 8  20 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetForm () {
      this.$refs.form.resetFields()
    },
    submitForm () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.postData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    postData () { // 
      // console.log(CryptoJS.AES.encrypt('1111', '123456'))
      // var jiami = CryptoJS.AES.encrypt(this.form.password, 'bigmind').toString() // 
      // var bytes = CryptoJS.AES.decrypt(jiami, 'bigmind').toString(CryptoJS.enc.Utf8) // 
      this.form.password = CryptoJS.AES.encrypt(this.form.password, 'bigmind').toString() // 
      this.jsondata.postlogin(this.form).then(response => {
        sessionStorage.setItem('Token', response.data)
        if (response.data === '') {
          this.infoLogin = true
          document.getElementById('infoLogin').innerHTML = ''
        } else {
          // console.log(sessionStorage.getItem('Token'))
          this.$router.push('/index')
        }
      })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>
<style>
.class1{
  color: #f00;
  text-align: center;
  display: block !important;
}
</style>



